* {
    font-family: "Roboto", sans-serif; }

$seablue: #1eacc7;
$coral: #ff5656;

header {
    position: relative;
    height: 50px; }

nav {
    height: 50px;
    background-color: #212121;
    width: 100%;
    position: fixed;
    z-index: 2; }

.menu {
    display: flex;
    justify-content: space-between;
    padding: 13px 70px;
    margin: 0px;
    list-style: none; }

.menu_link {
    color: #ffffff;
    font-size: 15px;
    font-weight: 300;
    text-align: left;
    text-decoration: none; }

.menu_item {
    border-right: 2px dotted black;
    padding-right: 28px; }

.subheader {
    height: 84px;
    background-color: rgba(0, 0, 0, 0.6);
    position: absolute;
    z-index: 1;
    top: 50px;
    width: 100%; }

.subheader_logo {
    max-width: 100%;
    display: block;
    margin-top: 16px; }

.subheader_official {
    margin-top: 6px;
    opacity: 0.5;
    color: #ffffff;
    font-size: 13px;
    font-weight: 400;
    text-align: left; }

.subheader_call {
    color: #ffffff;
    font-size: 12px;
    font-weight: 300;
    line-height: 14px;
    text-align: right;
    margin-top: 19px; }

.subheader_phone {
    color: #ffffff;
    font-size: 22px;
    font-weight: 400;
    text-align: right;
    text-transform: uppercase;
    line-height: 12px;
    display: block;
    margin-top: 8px; }

.subheader .subheader_btn, .promo_btn {
    margin-top: 19px;
    width: 172px;
    height: 46px;
    box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
    border: none;
    border-radius: 4px;
    background-image: linear-gradient(to top, $seablue 0%, #4ce2ff 100%);
    text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
    color: #ffffff;
    font-size: 14px;
    font-weight: 400;
    text-align: center;
    text-transform: uppercase; }

.promo {
    min-height: 80px;
    background: url(../img/uber/bg/main_bg.png) center (center / cover) no-repeat;
    padding: 150px 0 135px 0; }

.promo_header {
    color: #ffffff;
    text-align: center;
    text-transform: uppercase;
    font-size: 60px;
    font-weight: 400;
    line-height: 60px; }

.promo_subheader {
    color: #ffffff;
    font-size: 36px;
    text-align: center;
    text-transform: uppercase;
    font-weight: 700;
    line-height: 36px;
    margin-top: 23px; }

.promo_descr {
    margin-top: 23px;
    color: #ffffff;
    font-size: 18px;
    font-weight: 400;
    line-height: 24px;
    text-align: center; }

.promo_btn {
    display: block;
    margin: 118px auto 0 auto; }

.reason {
    min-height: 900px;
    padding: 71px 0 113px; }

.label {
    color: #ffffff;
    font-size: 11px;
    font-weight: 400;
    text-transform: uppercase;
    background-color: black;
    width: 112px;
    height: 24px;
    padding: 5px 12px;
    margin: 0 auto;
    text-align: center;

    &_white {
        background-color: #ffffff;
        color: #070716; } }

.title {
    margin-bottom: 0;
    margin-top: 38px;
    color: #222222;
    font-size: 38px;
    font-weight: 700;
    text-align: center;
    text-transform: uppercase;
    &_white {
        color: #ffffff;
        margin-top: 32px; }

    &_terms {
        margin-top: 33px; } }

.subtitle {
    margin-top: 19px;
    color: #222222;
    font-size: 17px;
    font-weight: 400;
    text-align: center;
    &_white {
        color: #ffffff; } }

.reason .reason_item {
    display: flex;
    text-justify: center;
    align-items: center;
    margin-top: 35px; }

.reason_round {
    width: 113px;
    height: 113px;
    background-color: $seablue;
    border-radius: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 26px; }

.reason_descr {
    width: 342px;

    .reason_subtitle {
        line-height: 21px;
        text-align: justifyLeft;
        color: $seablue;
        font-size: 17px;
        font-weight: 400; }

    .reason_text {
        color: #202020;
        font-size: 15px;
        font-weight: 300;
        line-height: 21px;
        text-align: justify; } }

.mobile {
    min-height: 864px;
    padding: 48px 0 79px 0;
    background: url('../img/uber/bg/mobile_bg.png') center center/cover no-repeat;

    .row {
        margin-top: 32px; }

    &_item {
        width: 320px;
        height: 280px;
        margin: 0 auto;
        margin-bottom: 19px;
        position: relative;

        &_1 {
            background: url('../img/uber/mobile_app/img_1.png') center center/cover no-repeat; }
        &_2 {
            background: url('../img/uber/mobile_app/img_2.png') center center/cover no-repeat; }
        &_3 {
            background: url('../img/uber/mobile_app/img_3.png') center center/cover no-repeat; }
        &_4 {
            background: url('../img/uber/mobile_app/img_4.png') center center/cover no-repeat; }
        &_5 {
            background: url('../img/uber/mobile_app/img_5.png') center center/cover no-repeat; }
        &_6 {
            background: url('../img/uber/mobile_app/img_6.png') center center/cover no-repeat; }

        &_subtitle {
            padding-top: 92px;
            color: #ffffff;
            font-size: 24px;
            font-weight: 400;
            line-height: 31px;
            text-align: center;
            text-transform: uppercase; }
        &_plus {
            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
            bottom: 35px;
            left: 50%;
            transform: translateX(-50%);
            width: 26px;
            height: 26px;
            background-color: #000000;
            margin: 0 auto;
            border-radius: 100%;
            cursor: pointer; } } }
.choice {
    min-height: 896px;
    background: url('../img/uber/bg/mobile_bg.png') center center/cover no-repeat;

    .black {
        height: 499px;
        padding: 57px 0 0 0;
        background-color: #070716;

        img {
            display: block;
            margin: 16px auto 0 auto; }

        .choice_descr {
            margin-top: 4px;
            color: #222222;
            font-size: 17px;
            font-weight: 300;
            line-height: 24px;
            text-align: center; } } }

.terms {
    min-height: 919px;
    padding: 68px 0 84px 0;

    .row {
        display: flex;
        justify-content: space-around;

        .col-md-6 {
            margin-bottom: 62px;
            width: 478px;
            min-height: 364px;
            font-size: 17px;
            font-weight: 300;

            .terms_subtitle {
                font-size: 22px;
                font-weight: 400;
                color: $seablue; }

            .red {
                color: $coral; }

            ul {
                padding: 0;
                margin-bottom: 0;

                li {}
                list-style-type: none;
                line-height: 40px;

                .tick {
                    width: 21px;
                    height: 20px;
                    color: $seablue;
                    margin: 0 12px 0px 19px; } }
            .terms_descr {
                color: $seablue; } } } }
